
.arena {
  background: rgb(2,0,36);
  /* background: radial-gradient(circle, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 35%, rgba(0,212,255,1) 100%); */
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

button:active {
  opacity: .7;
}
.goback {
  font-size: 20pt;
  color: #fff;
  background: none;
  border: none;
}

.table {
  position: absolute;
  width: 850px;
  border: 10px solid var(--felt-border-color);
  height: 400px;
  border-radius: 250px;
  left: 50%;
  margin-left: -425px;
  top: 50%;
  margin-top: -250px;
  background: var(--felt-color);
  box-shadow: 3px 3px 5px #555;
  perspective: 400px;
  :before {
    content: '';
    border: 1px solid #555;
    position: absolute;
    width: 78%;
    height: 18%;
  }
}

.player-stack {
  position: absolute;
  left: 50%;
  width: 90%;
  margin-left: -45%;
  /*border: 2px solid #555;*/
  background-color: #555;
  color: #fff;
  padding: 3px 0;
  border-radius: var(--border-radius);
  text-align: center;
  top: 48%;
  z-index: 3;
}
.player-hand {
  position: absolute;
  left: 25%;
  width: 50%;
  text-align: center;
  bottom: 43%;
  z-index: 2;
  padding: 7px 0;
  background: rgba(255,255,255,.05);
  border-radius: var(--border-radius);
}
.player-name {
  position: absolute;
  top: 75%;
  text-align: center;
  width: 90%;
  left: 5%;
  background: rgba(30, 30, 30, .5);
  color: #fff;
}
.player-bet {
  position: absolute;
  color: #fff;
}
.hand-strength {
  position: absolute;
  width: 100%;
  bottom: -15px;
  color: #aaa;
  font-size: .8em;
  text-align: center;
  font-style: italic;
}
.player {
  position: absolute;
  padding: 4px;
  /* border: 1px solid #ddd; */
  width: 200px;
  height: 100px;
  /* transform: rotateX(5deg);; */
}
.sit-here-button {
  position: absolute;
  width: 200px;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.player-sittingout, .player-folded {
  opacity: .9;
}
.player-sittingout .player-name:after {
  content: ' (sitting out)';
  font-size: .8em;
  font-style: italic;
}
.player-folded .player-name:after {
  content: ' (folded)';
  font-size: .8em;
  font-style: italic;
}
.dealer {
  position: absolute;
  display: block;
  font-size: 10pt;
  width: 1em;
  height: 1em;
  line-height: 1em;
  text-align: center;
  border-radius: .7em;
  background-color: var(--fold-color);
  font-weight: bold;
  border: 2px solid #222;
  transform: rotateX(10deg);
  z-index: 2;
}
