.player-video {
  border: 1px solid #000;
  width: 250px;
}
.playerseat-1 { bottom: -50px; left: calc(50% + 50px); }
.playerseat-2 { bottom: -50px; right: calc(50% + 50px); }
.playerseat-3 { top: calc(50% - 48px); left: -100px; }
.playerseat-4 { top: -50px; right: calc(50% + 50px); }
.playerseat-5 { top: -50px; left: calc(50% + 50px); }
.playerseat-6 { top: calc(50% - 48px); right: -100px; }

.playerseat-1 .player-bet { top: -20px; left: 0; }
.playerseat-2 .player-bet { top: -20px; right: 10px; }
.playerseat-3 .player-bet { top: 20px; right: -50px; }
.playerseat-4 .player-bet { bottom: -20px; right: 0; }
.playerseat-5 .player-bet { bottom: -20px; left: 0; }
.playerseat-6 .player-bet { top: 20px; left: -50px; }

.dealer-1 { bottom: 20px; left: calc(50% + 50px); }
.dealer-2 { bottom: 20px; right: calc(50% + 50px);  }
.dealer-3 { top: 59%; left: 120px; }
.dealer-4 { top: 45px; right: calc(50% + 20px); }
.dealer-5 { top: 55px; right: 140px; }
.dealer-6 { top: 59%; right: 120px; }

